import React from 'react';
import {Link} from 'react-scroll'

export class OrderBtn extends React.Component {
	render() {
		return(
				<Link to={'contacts'} smooth={true}>
					<button className="order-btn">Заказать</button>
				</Link>
		);
	}
}

export default OrderBtn;


