import React from 'react';

export class miniMenu extends React.Component {
	handleMenuClick() {
		$(".mini-menu").toggleClass("on");
		$(".nav-menu").toggle();
		$("body").toggleClass("no-scroll");
	}

	render() {
		return(<div className="col-sm-2 order-sm-2 order-1 offset-sm-1 col-4 mini-wrapper">
				<button onClick={this.handleMenuClick} className="mini-menu"><span></span></button>
			</div>);
	}
}

export default miniMenu


