import React from 'react';


class Employer extends React.Component {
  render(){
    const keys = Object.keys(this.props.links);

    return (<div className="about-item">
            <div className="social-block">
              {keys.map(key =>
                <a target="_blank" key={key} href={this.props.links[key]}><i className={'fa fa-'+ key}></i></a>
              )}
            </div>
            <div className="content-block">
              <div className="center-block">
                <div className="image-wrapper">
                  <img src={this.props.avatar} />
                </div>
              </div>
              <h1 className="name">{this.props.name}</h1>
              <h1 className="position">{this.props.special}</h1>
              <p className="description-block">
                {this.props.description}
              </p>
            </div>
            <div className="mobile-info"><i className="fa fa-info-circle"></i> Чтобы увидеть больше, сдвиньте влево.</div>
          </div>);
  }
}

export default Employer;




