import React from 'react';
import OwlCarousel from 'react-owl-carousel2';
import LPImage from '../../img/landingpage.png'; // {LPImage: '1231.png'}
import ImageCorporate from '../../img/analytics.png';
import StoreImage from '../../img/onlineshop.png';
import ImageSeo from '../../img/seo.png';

import Service from './service';
import LandingContent from './landing_page';
import InternetStoreContent from './internet_store';
import CorporateSiteContent from './corporate_site';
import SeoContent from './seo';
 
const options = {
    loop: false,
    margin: 30,
	autoWidth: true,
	nav: true,
    navText: ["<i class='fa fa-angle-left'></i>","<i class='fa fa-angle-right'></i>"]
};

const data = {
	landing: {
		id: 1,
		name: 'Landing Page',
		image: LPImage,
		price: 'от 5 000',
		dom: LandingContent,
	},
	internet_store:{
		id: 2,
		name: 'Интернет-магазин',
		image: StoreImage,
		price: 'от 15 000',
		dom: InternetStoreContent,
	},
	corporate:{
		id: 3,
		name: 'Корпоративный сайт',
		image: ImageCorporate,
		price: 'от 20 000',
		dom: CorporateSiteContent,
	},
	seo:{
		id: 4,
		name: 'Продвижение сайтов, страниц',
		image: ImageSeo,
		price: 'от 3 000',
		dom: SeoContent,
	},
};

export class Services extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			servicesInfo: data.landing.dom,
			selectedService: data.landing.id,
		};
		this.setNewInfo = this.setNewInfo.bind(this);
	}
	setNewInfo(state) {
		this.setState({ servicesInfo: state.dom, selectedService: state.id });
	}

	render() {
		return(
			<div id="services">
		<div className="services-top">
			<div className="s-wrap">
				<div id="wrapper">
					<div className="container-fluid">
						<div className="row">
							<OwlCarousel options={options} className="owl-carousel offset-sm-1 col-sm-11">
								<Service
									setNewInfo={this.setNewInfo}
									isSelectedService={this.state.selectedService == data.landing.id}
									{...data.landing}
								/>
								<Service
									setNewInfo={this.setNewInfo}
									isSelectedService={this.state.selectedService == data.internet_store.id}
									{...data.internet_store}
								/>
								<Service
									setNewInfo={this.setNewInfo}
									isSelectedService={this.state.selectedService == data.corporate.id}
									{...data.corporate}
								/>
								<Service
									setNewInfo={this.setNewInfo}
									isSelectedService={this.state.selectedService == data.seo.id}
									{...data.seo}
								/>
							</OwlCarousel>
						</div>
					</div>
				</div>
			</div>
		</div>
		<div className="services-content">
			<div id="wrapper">
				<div className="container-fluid">
					{this.state.servicesInfo()}
				</div>
			</div>
		</div>
		</div>);
	}
}

export default Services;



