import React from 'react';
import OrderBtn from '../order-btn';
import HeaderImage from '../../img/desktop.png'

function Preview(props) {
	const titles = props.title;
	const titleItems = titles.map((title, key) =>
		<h1 key={key}>{title}</h1>
	);
	return (<div className="title">{titleItems}</div>);
}

function Description(props) {
	const text = props.text;
	const descriptions = props.description;
	const descItems = description.map((description, key) =>
		<li key={key}>{description}</li>
	);
	return(<div className="description">
		<p>{props.text}</p>
		<ul>{descItems}</ul>
	</div>);
}

const text = 'В разработке сайта мы используем:';
const description = ['Новейшие технологии;', 'Уникальный и современный дизайн;', 'Разработка в кратчайшие сроки;', 'Просмотр результатов на любом этапе и внесение своих корректив;'];
const title = ['Быстро.', 'Стильно.', 'Современно.'];
export class HeaderContent extends React.Component {
	render() {
		return(
		<div id="main" className="header-content">
			<div id="wrapper">
				<div className="container-fluid">
					<div className="row">
						<div className="col-lg-4 col-md-6 offset-sm-1">
							<Preview title={title} />
							<Description text={text} description={description} />
							<OrderBtn />
						</div>
					</div>
				</div>
			</div>
			<img className="header-image col-xl-6 col-lg-7 col-md-6" src={HeaderImage} />
		</div>);
	}
}

export default HeaderContent;


