import React from 'react';


export default class Service extends React.Component {
	constructor(props) {
		super(props);
		this.handleSendInfo = this.handleSendInfo.bind(this);
	}

	handleSendInfo() {
    var value = {dom: this.props.dom, id: this.props.id};
		this.props.setNewInfo(value);
	}
	render() {
    return(<div onClick={this.handleSendInfo} className={this.props.isSelectedService?"chosenService item":"item"}>
          <h1 className="s-name">{this.props.name}</h1>
          <div className="s-content">
            <img className="view-image" src={this.props.image} />
            <div className="price">
              <h1>{this.props.price} <i className="fa fa-ruble"></i></h1>
            </div>
            <button className="more">Подробнее</button>
          </div>
        </div>);
	}
}




