import React from 'react';

export class Contacts extends React.Component {
    constructor(props) {
        super(props);
        this.state= {name: '', contact: '', message: ''};
        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }
    handleChange(event) {
        this.setState({
            [event.target.name]: event.target.value
        });
    }
    handleSubmit(event) {
        event.preventDefault();
        var name = this.state.name.trim();
        var contact = this.state.contact.trim();
        if (!name || !contact) {
            return;
        }
        var form = new FormData();
        form.append('name', this.state.name);
        form.append('contact', this.state.contact);
        form.append('message', this.state.message);
        fetch('https://warm-plateau-30269.herokuapp.com/send_message/teamwork/', {
            method: 'post',
            body: form
        }).then().then(function (data) {
            console.log('Request succeeded with JSON response', data);
        }).catch(function (error) {
            console.log('Request failed', error);
        });
        this.setState({name: '', contact: '', message: ''});
    }
    render() {
        return (
            <div id="contacts">
                <div id="wrapper">
                    <div className="container-fluid">
                        <div className="row">
                            <div className="col-md-4 col-sm-8 offset-sm-1">
                                <h1 className="title">Контакты</h1>
                                <h1 className="question">Как с нами связаться?</h1>
                                <p className="description">Для того, чтобы с нами связаться, вам нужно заполнить форму заявки, после чего мы обязательно, в течении одного рабочего дня, свяжемся с вами.</p>
                                <div className="phone-number">
                                    <h1>Номер телефона:</h1>
                                    <h2><i className="fa fa-phone"></i> +7-(904)-995-2345</h2>
                                </div>
                                <div className="location">
                                    <h1>Город:</h1>
                                    <h2><i className="fa fa-map-marker"></i> Новокузнецк</h2>
                                </div>
                            </div>
                            <div className="col-md-5 col-sm-10 offset-sm-1">
                                <form onSubmit={this.handleSubmit} className="contacts-form">
                                    <div className="input-block">
                                        <input name="name" placeholder="ФИО" type="text" value={this.state.name} onChange={this.handleChange} />
                                        <span><i className="fa fa-address-card"></i> ФИО</span>
                                    </div>
                                    <div className="input-block">
                                        <input name="contact" placeholder="@Почта" type="text" value={this.state.contact} onChange={this.handleChange} />
                                        <span><i className="fa fa-envelope"></i> @Почта</span>
                                    </div>
                                    <div className="textarea-block">
                                        <textarea name="message" placeholder="Комментарии" value={this.state.message} onChange={this.handleChange}></textarea>
                                        <span><i className="fa fa-comment"></i> Комментарии</span>
                                    </div>
                                    <button type="submit">Отправить</button>
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Contacts;



