import React from 'react';
import OrderBtn from '../order-btn';
import MiniMenu from './m-menu';
import {Link} from 'react-scroll';
import Logo from '../../img/logo.png';

function Logotype(props) {
	return (<div className="col-xl-2 col-lg-2 offset-lg-1 order-lg-1 col-sm-6 order-sm-3 order-4 col-4">
							<a href="/" className="logotype"><img src={Logo} alt={props.name} /></a>
						</div>);
}

class MenuPoint extends React.Component {
	handleMenuClick() {
		$(".mini-menu").toggleClass("on");
		$(".nav-menu").toggle();
		$("body").toggleClass("no-scroll");
	}

	render() {
		return (
				<li>
					<Link to={this.props.scrollid} smooth={true} offset={-90} duration={200} onClick={this.handleMenuClick}>
						{this.props.name}
					</Link>
				</li>
		)
	};
};

