// const lists = ['Главная', 'Услуги', 'О компании', 'Наши работы', 'Контакты'];
export class Navtop extends React.Component {

	render() {
		return(<div className="nav-top">
			<div id="wrapper">
				<div className="conteiner-fluid">
					<div className="row">
						<Logotype name="TMW" />
						<MiniMenu/>
						<div className="col-xl-6 col-lg-6 offset-lg-0 order-lg-2 col-12 nav-wrapper">
							<nav className="nav-menu">
								<ul >
									<MenuPoint key="main" name={'Главная'} scrollid="main"  />
									<MenuPoint key="services" name={'Услуги'} scrollid="services" />
									<MenuPoint key="about" name={'О компании'} scrollid="about" />
									<MenuPoint key="portfolio" name={'Наши работы'} scrollid="works" />
									<MenuPoint key="footer" name={'Контакты'} scrollid="contacts" />
								</ul>
							</nav>
						</div>
						<div className="col-sm-2 col-4 order-last">
							<div className="btn-wrap">
								<OrderBtn/>
							</div>
							<Link to={'contacts'} smooth={true}>
								<button className="mini-order-btn"><i className="fa fa-shopping-cart"></i></button>
							</Link>
						</div>
					</div>
				</div>
			</div>
		</div>);
	}
}

export default Navtop;


