import React from 'react';


class Stat extends React.Component {
  render(){
  	return (<div className="col-lg-3 col-sm-6 count-block">
          <div className="count-center">
            <h1 className="count">{this.props.count}</h1>
            <p className="count-name">{this.props.description}</p>
          </div>
        </div>);
  }
}

export default Stat;



