import React from 'react';
import OwlCarousel from 'react-owl-carousel2';
import Work from './work';

import leventImage from '../../img/levent.png';
import leventMobileImage from '../../img/leventphone.png';
import avtootogrevImage from '../../img/avtootogrev.png';
import avtootogrevMobileImage from '../../img/avtootogrevphone.png';
import disswImage from '../../img/dissw.png';
import disswMobileImage from '../../img/disswphone.png';
import olimpMobileImage from '../../img/olimpmobile.png';
import olimpImage from '../../img/olimpweb.png';

const options = {
	loop: true,
	items: 1,
    autoWidth: false,
    autoHeight: true,
	nav: true,
	navText: ["<i class='fa fa-angle-left'></i>","<i class='fa fa-angle-right'></i>"]
};

const data = {
	levent: {
		project_name: 'Магазин шуб «Levent & Vualle»',
		main_image: leventImage,
		mobile_image: leventMobileImage,
		description:
				`Интернет-каталог для рекламы верхней одежды, реализован
				в очень короткие сроки. Включает в себя панель администратора для управления содержимым, 
				адаптивный дизайн и отправку заявок для обратной связи. Содержит в себе более 100 страниц.`,
		url: 'http://www.levent-nk.ru/'
	},
	olimp:{
		project_name: 'Интернет-магазин для СК "ОЛИМП" Новокузнецк',
		main_image: olimpImage,
		mobile_image: olimpMobileImage,
		description: `Сайт с системой онлайн оплаты, и админ-панелью для добавления новых услуги и категорий, с возможностью создания подкатегорий неограниченной глубины`,
		url: 'https://olimp.dissw.ru/'
	},
	avtootogrev:{
		project_name: 'Landing Page для рекламы услуг "Автоотогрев Новокузнецк"',
		main_image: avtootogrevImage,
		mobile_image: avtootogrevMobileImage,
		description:
				`Разработан легковесный, лаконичный дизайн по требованиям заказчика,
				 который служит для рекламы услуг. В данный момент сайт находится на первом месте в поисковой выдаче
				  и приносит большой поток клиентов в "холодные" дни.`,
		url: 'http://avtootogrev-nvkz.ru/'
	},
	dissw:{
		project_name: 'Информационно-вычислительный центр «DISSW team»',
		main_image: disswImage,
		mobile_image: disswMobileImage,
		description: `Информационный сайт, включающий в себя множество динамических страниц с системой модерации и администрирования контента.`,
		url: 'https://dissw.ru/'
	}
};

class Works extends React.Component {
	render() {
		const btnStyle = {
			visibility: 'hidden'
		};
		return(<div id="works">
			<OwlCarousel ref="work" options={options} className="owl-carousel work-carousel">
				<Work disabledPrev={btnStyle} nextRef={() => this.refs.work.next()} prevRef={() => this.refs.work.prev()}
					{...data.levent}
				/>
				<Work disabledPrev={btnStyle} nextRef={() => this.refs.work.next()} prevRef={() => this.refs.work.prev()}
					{...data.olimp}
				/>
				<Work nextRef={() => this.refs.work.next()} prevRef={() => this.refs.work.prev()}
					{...data.avtootogrev}
				/>
				<Work disabledNext={btnStyle} nextRef={() => this.refs.work.next()} prevRef={() => this.refs.work.prev()}
					{...data.dissw}
				/>
			</OwlCarousel>
			</div>);
	}
}

export default Works


