import React from 'react';

class Work extends React.Component {
	render() {
		const styles = {
			backgroundImage: `url(${this.props.main_image})`
		};
		return(<div className="item" style={styles}>
				<div className="work-content">
				<div className="mobile-info"><i className="fa fa-info-circle"></i> Чтобы увидеть больше, сдвиньте влево.</div>
					<div id="wrapper">
						<div className="container-fluid">
							<div className="row">
								<div className="col-md-5 col-sm-10 offset-sm-1">
									<div className="wrap-block">
										<h1 className="title">
											Наши работы
										</h1>
										<h1 className="project-name">
											{this.props.project_name}
										</h1>
										<p className="description">
											{this.props.description}
										</p>
										<a target="_blank" href={this.props.url}><button className="show-site">Перейти на сайт</button></a>
										<div className="button-wrapper">
											<button onClick={this.props.nextRef} style={this.props.disabledNext} className="next">Следующий <i className="fa fa-angle-right"></i></button>
											<button onClick={this.props.prevRef} style={this.props.disabledPrev} className="prev"><i className="fa fa-angle-left"></i> Предыдущий</button>
										</div>
									</div>
								</div>
								<div className="col-md-5">
									<div className="wrap-image"><img className="phone-image" src={this.props.mobile_image} />
										<div className="panel-info">
											<a className="icons" data-title="Адаптивный"><i className="fa fa-mobile"></i></a>
											<a className="icons" data-title="Креативный"><i className="fa fa-lightbulb-o"></i></a>
											<a className="icons" data-title="Функциональный"><i className="fa fa-cog"></i></a>
										</div>
									</div>
								</div>
							</div>
						</div>
					</div>
				</div>
            </div>);
	}
}

export default Work


